.galleryItem {
    position: relative;
    display: inline-block;
    overflow: hidden;
    border-radius: 2px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.itemImg {
    height: 260px;
    object-fit: cover;
    margin: 0 auto;
}

.stats {
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #fafcee;
    user-select: none;
    color: #3b4221;
    padding: 4px;
}

.statsItem {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
}

.fullscreenButton {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    font: inherit;
    background-color: rgba(215, 47, 47, 0.4);
    border-radius: 50%;
    width: 56px;
    height: 56px;
    color: #ffffff;
    cursor: pointer;
    transition: background-color 100ms linear, opacity 100ms linear;
    opacity: 0;
}

.fullscreenButton:hover,
.fullscreenButton:focus {
    background-color: rgba(120, 241, 89, 0.7);
}

.buttonIcon {
    font-size: 36px;
}

.galleryItem:hover .fullscreenButton,
.galleryItem:focus .fullscreenButton {
    opacity: 1;
}
